.Entete {
    display: flex;
    justify-content: space-between;
    align-items: center;

    position: sticky;
    top: 0;

    background-color: #444;
    color: white;

    h1 {
        font-size: 1.5rem;
        padding: 1rem 2rem;
    }

    nav {
        padding: 1rem 2rem;

        display: flex;
        align-items: center;

        a {
            font-size: 1.25em;
            
            &:not(:last-child) {
                padding-right: .75rem;
            }
        }
    }
}